.my-posts-form {
	padding-bottom: 30px; }

.article-item {
	margin-bottom: 25px;
	&__article {
		display: block;
		position: relative;
		padding: 21px 12px 5px;
		border: 1px solid #e3e3e3;
		.cv {
			border-top: 1px solid transparent;
			background-color: transparent;
			margin-bottom: 10px;
			transition: border-color .3s; }
		&:hover {
			box-shadow: 0 0 10px rgba(0,0,0,.2);
			.btn-panel {
				transform: scale(1);
				height: auto;
				z-index: 2; }
			.cv {
				border-top: 1px solid #d0ccc5;
				background-color: #f1ede7; } } }
	&__title {
		font-size: 21px;
		margin-bottom: 10px; }
	&__btn-block {
		text-align: center; } }
