.banner-advertising {
	&__title {
		text-transform: none;
		margin-bottom: 15px; }

	.ritual-agent {
		&__title {
			text-transform: none; }
		&__label {
			border: none;
			width: 100%;
			&:after {
				top: 70px; }
			&:before {
				top: 70px; }
			&:hover {
				.cv {
					border-top: 0;
					background-color: transparent; } } } } }


.banner-adver {
	&__col-header {
		width: 100%;
		height: 74px;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%; }
	&__row {
		display: flex;
		align-items: center; }
	&__left-col {
		flex: 1 1 5%; }
	&__right-col {
		flex: 1 1 95%;
		margin-right: 40px; }
	&__col-footer {
		.cv {
			border-top: 0;
			background-color: $white;
			&__views {
				display: flex;
				align-items: center; }
			&__views-icon {
				margin-right: 10px;
				.icon {
					fill: #b1a79b;
					font-size: 18px; } }
			&__views-icon_mod {
				.icon {
					font-size: 14px; } }
			&__text {
				color: #b1a79b; }
			&:hover {
				border-top: 0;
				background-color: $white; } } } }

.banner-scale {
	display: flex;
	align-items: flex-end;
	&__line {
		display: block;
		width: 3px;
		background-color: #dcdddd;
		&:first-child {
			height: 9px; }
		&:nth-child(2) {
			height: 12px; }
		&:nth-child(3) {
			height: 15px; }
		&:not(:last-child) {
			margin-right: 3px; }
		&.green {
			background-color: #67ac65; } } }
