.user-account {
	&__wrap {
		padding-top: 70px; }
	&__row {
		margin-bottom: 20px; }
	&__info-list {
		text-align: center;
		font-size: 15px;
		color: #b7aea3;
		border-bottom: 1px solid #eee;
		padding-bottom: 10px;
		margin-bottom: 10px; }
	&__nav-list {
		padding-left: 40px; }
	&__nav-item {
		font-size: 15px;
		color: $brown;
		padding: 5px 0; }
	&__nav-link {
		display: block;
		padding: 5px 0;
		&.is-active {
			font-weight: 600; }
		&:hover {
			font-weight: 600; }
		i {
			margin-right: 15px; } } }


